es/elements/card.scss';\n@import './styles/elements/details.scss';\n\n\n* {\n    font-family: Montserrat, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Droid Sans, Helvetica Neue, sans-serif;\n}\n\nhtml {\n    margin: 0px;\n    height: 100%;\n    font-size: 17px;\n    font-weight: normal;\n    background-color: $background;\n}\n\nbody {\n    margin: 0px;\n    height: 100%;\n}\n\n*, *:before, *:after {\n    box-sizing: border-box;\n}\n\n\n::-moz-selection {\n    background: $selection;\n    color: #333\n}\n\n::-webkit-selection {\n    background: $selection;\n    color: #333\n}\n\n::selection {\n    background: $selection;\n    color: #333\n}"],"sourceRoot":""}]);
